<template>
	<v-dialog v-model="proxyIsOpened" max-width="1216px">
		<v-card class="pa-10 pb-0">
			<v-flex class="d-flex justify-end">
				<v-btn
					icon
					@click="closeThis"
				>
					<v-icon>mdi-close</v-icon>
				</v-btn>
			</v-flex>
			<v-card-text class="pa-0">
				<v-container>
					<v-row>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.name" label="Имя"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.nameRu" label="Name for Main Page (RU)"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.nameEn" label="Name for Main Page (EN)"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.promoPrice" label="Promo Price" v-money="money"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.initialFee" label="Initial Free"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.carcaseId" label="Carcase ID"></v-text-field>
						</v-col>
					</v-row>
					<v-row>
						<v-col cols="12" sm="6" md="4">
							<div class="subtitle mb-2 black--text">Изображение на главной в конфигураторе</div>
							<v-img :src="require('@/assets/img/RS6.jpg')">
								<template v-slot:placeholder>
									<v-row
										class="fill-height ma-0"
										align="center"
										justify="center"
									>
										<v-progress-circular indeterminate color="success"></v-progress-circular>
									</v-row>
								</template>
							</v-img>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<div class="subtitle mb-2 black--text" color="black">File Image B</div>
							<v-img :src="require('@/assets/img/RS6.jpg')">
								<template v-slot:placeholder>
									<v-row
										class="fill-height ma-0"
										align="center"
										justify="center"
									>
										<v-progress-circular indeterminate color="success"></v-progress-circular>
									</v-row>
								</template>
							</v-img>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<div class="subtitle mb-2 black--text">Image Big (1140x900)</div>
							<v-img :src="require('@/assets/img/RS6.jpg')">
								<template v-slot:placeholder>
									<v-row
										class="fill-height ma-0"
										align="center"
										justify="center"
									>
										<v-progress-circular indeterminate color="success"></v-progress-circular>
									</v-row>
								</template>
							</v-img>
						</v-col>
					</v-row>
					<v-row>
						<v-col cols="12" md="12" class="d-flex">
							<v-file-input label="Загрузить фото" class="mr-3"></v-file-input>
							<v-file-input label="Загрузить цены и комплектации" class="mr-3"></v-file-input>
							<v-file-input label="загрузить price list"></v-file-input>
						</v-col>
					</v-row>
					<v-row>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.priceId" label="Price ID" disabled></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.priceName" label="Name"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.priceShortDescription" label="Short Description"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.priceCarTypeId" label="Car Type ID" disabled></v-text-field>
						</v-col>
					</v-row>
				</v-container>
			</v-card-text>

			<v-card-actions class="pa-0">
				<v-container>
					<v-row>
						<v-col>
							<v-divider></v-divider>
						</v-col>
					</v-row>
					<v-row>
						<v-col>
							<v-btn color="success" dark @click="closeThis">Сохранить</v-btn>
						</v-col>
					</v-row>
				</v-container>
			</v-card-actions>
		</v-card>
	</v-dialog>
</template>

<script>
import {VMoney} from 'v-money'

export default {
	name: 'ModelsPopup',
	props: {
		isOpened: {
			type: Boolean,
			requared: true
		}
	},
	directives: {money: VMoney},
	data () {
		return {
			editedItem: {
				name: '',
				nameRu: '',
				nameEn: '',
				promoPrice: 0,
				initialFee: 0,
				carcaseId: 0,
				priceId: 888,
				priceName: '',
				priceShortDescription: '',
				priceCarTypeId: 'Creta'
			},
			money: {
				thousands: ' ',
				precision: 0
			}
		}
	},
	computed: {
		proxyIsOpened: {
			get () {
				return this.isOpened
			},
			set () {
				this.$emit('close-dialog')
			}
		}
	},
	methods: {
		closeThis () {
			this.$emit('close-dialog')
		}
	}
}
</script>

<style>

</style>