import Vue from 'vue'
import VueRouter from 'vue-router'
import Models from '../views/Models.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Models',
    component: Models
  },
  {
    path: '/car/:id',
    name: 'CarPage',
	component: () => import(/* webpackChunkName: "carpage" */ '../views/CarPage.vue'),
	meta: {
		title: 'Редактирование'
	}
  },
  {
    path: '/catalogs',
    name: 'Catalogs',
	component: () => import(/* webpackChunkName: "about" */ '../views/Catalogs.vue'),
	meta: {
		title: 'Справочники'
	}
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
