<template>
	<v-container>
		<v-row>
			<v-col>
				<v-toolbar height="60" elevation="2">
					<v-toolbar-title class="font-weight-bold">Все автомобили</v-toolbar-title>
				</v-toolbar>
			</v-col>
		</v-row>
		<v-row justify="end">
			<v-col cols="auto">
				<v-btn x-large color="success" dark justify-self="end"  class="mt-2 mb-2" @click="createNewCar">Cоздать тип машины</v-btn>
			</v-col>
		</v-row>
		<v-row>
			<v-col>
				<v-data-table
					:headers="headers"
					:items="models"
					:items-per-page="99999"
					:hide-default-footer="true"
					:loading="false"
					:single-expand="true"
					show-expand
					class="elevation-1"
					@click:row="goToCar"
				>
					<template v-slot:item.image="{ item }">
						<v-img :src="item.image" :alt="item.name" height="64" aspect-ratio="2">
							<template v-slot:placeholder>
								<v-row
									class="fill-height ma-0"
									align="center"
									justify="center"
								>
									<v-progress-circular indeterminate color="success"></v-progress-circular>
								</v-row>
							</template>
						</v-img>
					</template>
					<template v-slot:item.promo_price="{ item }">
						{{ item.promo_price | priceFormat }}
					</template>
					<template v-slot:item.action="{ item }">
						<v-tooltip top>
							<template v-slot:activator="{ on }">
								<v-icon
									class="mr-2"
									@click.stop="createNewCar"
									v-on="on"
								>
									edit
								</v-icon>
							</template>
							<span>Редактировать</span>
						</v-tooltip>
						<v-tooltip top>
							<template v-slot:activator="{ on }">
								<v-icon
									@click.stop="remove"
									v-on="on"
								>
									delete
								</v-icon>
							</template>
							<span>Удалить</span>
						</v-tooltip>
					</template>
					<template v-slot:expanded-item="{ headers, item }">
						<td :colspan="headers.length">
							<v-content class="pa-8">
								<div class="pt-8 pb-8">
									<strong class="mr-2">Name for Main Page (RU)</strong> <span color="grey lighten-3" class="mr-10">{{ item.name }}</span>
									<strong class="mr-2">Name for Main Page (EN)</strong> <span color="grey lighten-3">{{ item.name }}</span>
								</div>
								<v-divider></v-divider>
								<div class="pt-8 pb-4">
									<strong class="title font-weight-bold" color="black">Price list</strong>
								</div>
								<div class="d-flex pt-4 pb-8">
									<strong class="mr-2">ID:</strong> <span color="grey lighten-3" class="mr-10">{{ item.id }}</span>
									<strong class="mr-2">Name:</strong> <span color="grey lighten-3" class="mr-10">Загрузить цены и комплектации</span>
									<strong class="mr-2">Short Description:</strong> <span color="grey lighten-3">Creta.pdf</span>
									<strong class="mr-2">Car Type ID:</strong> <span color="grey lighten-3">Creta</span>
									<div class="ml-auto">
										<v-tooltip top>
											<template v-slot:activator="{ on }">
												<v-icon
													@click="remove"
													v-on="on"
												>
													delete
												</v-icon>
											</template>
											<span>Удалить</span>
										</v-tooltip>
									</div>
								</div>
							</v-content>
						</td>
					</template>
				</v-data-table>
				<models-popup :is-opened="dialog" @close-dialog="dialog = false"></models-popup>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
import ModelsPopup from '../components/ModelsPopup'

export default {
	name: 'Models',
	components: { ModelsPopup },
	data () {
		return {
			dialog: false,
			headers: [
				{
					text: '#',
					align: 'left',
					sortable: false,
					value: 'num',
				},
				{
					text: 'id',
					align: 'left',
					sortable: true,
					value: 'id',
				},
				{
					text: 'Name',
					align: 'left',
					sortable: true,
					value: 'name',
				},
				{
					text: 'Carcase ID',
					align: 'left',
					sortable: true,
					value: 'carcase',
				},
				{
					text: 'Initial fee',
					align: 'left',
					sortable: true,
					value: 'in_fee',
				},
				{
					text: 'Image',
					align: 'left',
					sortable: false,
					value: 'image',
				},
				{
					text: 'Promo Price',
					align: 'left',
					sortable: true,
					value: 'promo_price',
				},
				{
					text: '',
					align: 'left',
					sortable: false,
					value: 'action',
				},
				{ text: '', value: 'data-table-expand' }
			],
			models: [
				{
					num: 1,
					id: 22,
					name: 'Creta',
					carcase: 'кроссовер',
					in_fee: '47',
					image: require('@/assets/img/RS6.jpg'),
					promo_price: 7000
				},
				{
					num: 2,
					id: 23,
					name: 'Solaris',
					carcase: 'кроссовер',
					in_fee: '47',
					image: require('@/assets/img/RS6.jpg'),
					promo_price: 7000
				},
				{
					num: 3,
					id: 24,
					name: 'Santa Fe',
					carcase: 'кроссовер',
					in_fee: '47',
					image: require('@/assets/img/RS6.jpg'),
					promo_price: 7000
				},
				{
					num: 4,
					id: 25,
					name: 'Elantra',
					carcase: 'кроссовер',
					in_fee: '47',
					image: require('@/assets/img/RS6.jpg'),
					promo_price: 7000
				},
				{
					num: 5,
					id: 26,
					name: 'H-1',
					carcase: 'кроссовер',
					in_fee: '47',
					image: require('@/assets/img/RS6.jpg'),
					promo_price: 7000
				},
				{
					num: 6,
					id: 27,
					name: 'Tucson',
					carcase: 'кроссовер',
					in_fee: '47',
					image: require('@/assets/img/RS6.jpg'),
					promo_price: 7000
				}
			]
		}
	},
	methods: {
		createNewCar () {
			this.dialog = true
		},
		goToCar (car) {
			this.$router.push({ name: 'CarPage', params: { id: car.id } })
		},
		remove () {
			this.$root.$emit('alert', {text: 'Элемент удалён', type: 'success'})
		}
	}
}
</script>

