<template>
	<v-app>
		<v-app-bar app :height="64" color="white">
			<v-toolbar-items>
				<v-btn to="/" text>Модели</v-btn>
				<v-btn to="/catalogs" text>Справочники</v-btn>
				<v-btn text>Связи</v-btn>
				<v-btn text>Кредит</v-btn>
			</v-toolbar-items>
		</v-app-bar>		
		
		<v-content>
			<router-view></router-view>
		</v-content>

		<v-footer color="#D5D5D5" class="mt-12">
			<v-col
				class="text-center"
				cols="12"
			>
				{{ new Date().getFullYear() }} — <strong>Админка</strong>
			</v-col>
		</v-footer>
		<v-snackbar
			v-model="snackbar"
			:color="snackbarColor"
		>
			{{ snackbarText }}
			<v-btn
			dark
			text
			@click="snackbar = false"
			>
				Закрыть
			</v-btn>
		</v-snackbar>
	</v-app>
</template>

<script>
export default {
	name: 'App',
	components: {

	},
	data: () => ({
		snackbar: false,
		snackbarText: '',
		snackbarColor: ''
	}),
	methods: {
		alert (data) {
			if (this.snackbar) {
				this.snackbar = false

				setTimeout(() => {
					this.snackbarText = data.text
					this.snackbarColor = data.type

					this.snackbar = true
				}, 300);
			} else {
				this.snackbarText = data.text
				this.snackbarColor = data.type

				this.snackbar = true
			}
		}
	},
	mounted () {
		this.$root.$on('alert', this.alert)
	}
};
</script>
